.link {
  text-decoration: none;
  color: #52555f;
  display: flex;
  align-items: flex-end;
  margin: 0 auto 40px auto;
  width: 84px;
  font-size: 12px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.link:hover {
  transform: translatey(1px);
  box-shadow: none;
  color: #ff751d;
}

.span {
  margin-right: 15px;
  font-family: 'Roboto';
}

@media screen and (min-width: 768px) {
  .link {
    margin: 0;
  }
}
