.btn {
  width: 144px;
  min-height: 53px;
  border-radius: 30px;
  background-color: #fefefe;
  margin: 2px;
  border: 2px solid rgba(255, 117, 29, 0.2);
}

.deskWrap {
  padding: 24px 40px;
  background-color: #ffffff;
  box-shadow: 5px 10px 20px rgb(170 178 197 / 40%);
  border-radius: 0px 30px 30px 30px;
}

.text {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.02em;
  text-align: center;
  color: #52555f;
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .btn {
    min-width: 138px;
    padding: 14px;
    border-radius: 16px 16px 0 0;
    margin: 0;
    border: none;
  }

  .btn.isActive {
    box-shadow: none;
    background-color: #ff751d;
    color: #fff;
  }
}

@media screen and (min-width: 1280px) {
  .wrap {
    padding: 32px;
  }

  .mobileWrap {
    display: none;
  }

  .summaryTab {
    display: none;
  }

  .deskWrap {
    margin-bottom: 40px;
  }
}
@media screen and (max-width: 1279px) {
  .deskWrap {
    margin-bottom: 40px;
  }
}


@media screen and (max-width: 767.9px) {
  .transactionContainer {
    margin: 0 auto;
    width: fit-content;
  }

  .deskWrap {
    display: none;
  }

  .text {
    font-size: 12px;
    line-height: 14px;
  }

  .summaryTab {
    display: none;
  }

  .btnWrap {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
